'use strict';

// MODULES //

import isObject from '@stdlib/assert-is-plain-object' ;
import hasOwnProp from '@stdlib/assert-has-own-property' ;
import { isPrimitive as isBoolean } from '@stdlib/assert-is-boolean' ;
import { isPrimitive as isString } from '@stdlib/assert-is-string' ;
import format from '@stdlib/error-tools-fmtprodmsg' ;


// MAIN //

/**
* Validates function options.
*
* @private
* @param {Object} opts - destination object
* @param {Options} options - function options
* @param {string} [options.flags] - regular expression flags
* @param {boolean} [options.capture] - boolean indicating whether to wrap a regular expression matching a decimal number with a capture group
* @returns {(Error|null)} null or an error object
*
* @example
* var opts = {};
* var options = {
*     'flags': 'gm'
* };
* var err = validate( opts, options );
* if ( err ) {
*     throw err;
* }
*/
function validate( opts, options ) {
	if ( !isObject( options ) ) {
		return new TypeError( format( '0Qz2h', options ) );
	}
	if ( hasOwnProp( options, 'flags' ) ) {
		opts.flags = options.flags;
		if ( !isString( opts.flags ) ) {
			return new TypeError( format( '0Qz2i', 'flags', opts.flags ) );
		}
	}
	if ( hasOwnProp( options, 'capture' ) ) {
		opts.capture = options.capture;
		if ( !isBoolean( opts.capture ) ) {
			return new TypeError( format( '0Qz30', 'capture', opts.capture ) );
		}
	}
	return null;
}


// EXPORTS //

export default validate;
