'use strict';

/**
* Return a regular expression to match a decimal number.
*
* @module @stdlib/regexp-decimal-number
*
* @example
* import reDecimalNumber from '@stdlib/regexp-decimal-number' ;
*
* var RE_DECIMAL_NUMBER = reDecimalNumber();
*
* var bool = RE_DECIMAL_NUMBER.test( 'beep 1.0 boop' );
* // returns true
*
* bool = RE_DECIMAL_NUMBER.test( 'beep 1 boop' );
* // returns false
*
* @example
* import reDecimalNumber from '@stdlib/regexp-decimal-number' ;
*
* var RE_DECIMAL_NUMBER = reDecimalNumber({
*     'flags': 'gm'
* });
* var bool = RE_DECIMAL_NUMBER.test( 'beep 1.0 boop' );
* // returns true
*
* @example
* import reDecimalNumber from '@stdlib/regexp-decimal-number' ;
*
* var bool = reDecimalNumber.REGEXP.test( '2:3' );
* // returns false
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import REGEXP_CAPTURE from './regexp_capture.js' ;
import REGEXP from './regexp.js' ;


// MAIN //

setReadOnly( main, 'REGEXP', REGEXP );
setReadOnly( main, 'REGEXP_CAPTURE', REGEXP_CAPTURE );


// EXPORTS //

export default main;
